<template>
  <div>
    <div class="tw-p-3 tw-shadow-sm tw-bg-white tw-font-bold tw-text-xl">
      
    </div>
    <v-container grid-list-xs class="">
      <v-card class="pa-4">
        <v-row class="">
          <v-col cols="4" class="pa-6 tw-h-96 tw-bg-gray-100">
            <div class="tw-flex tw-flex-row tw-p-1 tw-mt-4">
              <div class="tw-w-32">id</div>
              <div class="tw-w-48">{{ basic.id }}</div>
            </div>
            <div class="tw-flex tw-flex-row tw-p-1">
              <div class="tw-w-32"></div>
              <div class="tw-w-48">{{ basic.name }}</div>
            </div>
            <div class="tw-flex tw-flex-row tw-p-1">
              <div class="tw-w-32"></div>
              <div class="tw-w-48">{{ basic.mail }}</div>
            </div>
          </v-col>
          <v-col class="">
            <v-tabs color="teal" v-model="choosen">
              <v-tab></v-tab>
              <v-tab></v-tab>
            </v-tabs>
            <v-tabs-items v-model="choosen">
              <v-tab-item class="pa-4">
                <v-form v-model="infoValid">
                  <v-text-field
                    color="teal"
                    label=""
                    :rules="rules.name"
                    v-model="editInfo.name"
                  ></v-text-field>
                  <v-text-field
                    color="teal"
                    label=""
                    :rules="rules.mail"
                    v-model="editInfo.mail"
                  ></v-text-field>
                </v-form>
                <v-btn color="primary" :disabled="!infoValid" @click="updateInfo()"></v-btn>
              </v-tab-item>
              <v-tab-item class="pa-4">
                <v-form v-model="passwordValid">
                  <v-text-field
                    color="teal"
                    label=""
                    type="password"
                    :rules="rules.password"
                    v-model="editPassword.old"
                  ></v-text-field>
                  <v-text-field
                    color="teal"
                    label=""
                    type="password"
                    :rules="rules.password"
                    v-model="editPassword.new"
                  ></v-text-field>
                  <v-text-field
                    color="teal"
                    label=""
                    type="password"
                    :rules="rules.repeatPassword"
                    v-model="editPassword.confirm"
                  ></v-text-field>
                </v-form>
                <v-btn color="primary" :disabled="!passwordValid" @click="updatePassword()"></v-btn>
              </v-tab-item>
            </v-tabs-items>
          </v-col>
          <v-col cols="2"></v-col>
        </v-row>
      </v-card>
    </v-container>

    <v-snackbar
      v-model="warn.doing"
      timeout=1000
    >
      {{warn.text}}
    </v-snackbar>

  </div>
</template>

<script>
import api from "../../../api/api";

export default {
  mounted() {
    this.getInfo();
  },
  activated() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      Promise.all([
        api.authFactory.getInfo(),
        api.authFactory.getGroups(),
      ]).then((resps) => {
        let info = resps[0];
        this.basic = info.content;
        this.editInfo.name = this.basic.name
        this.editInfo.mail = this.basic.mail
      });
    },
    startWarn(text) {
      this.warn.text = text
      this.warn.doing = true
    },
    updateInfo() {
      api.authFactory.editInfo(this.editInfo.name, this.editInfo.mail).then(resp => {
        if (resp.code == 0) {
          this.$router.push('info')
        } else if (resp.code == -1) {
          this.startWarn('')
        } else if (resp.code == -2) {
          this.startWarn('')
        }
      })
    },
    updatePassword() {
      api.authFactory.editPassword(this.editPassword.old, this.editPassword.new).then(resp => {
        if (resp.code == 0) {
          this.$router.push('info')
        } else if (resp.code == -1) {
          this.startWarn('')
        } else if (resp.code == -2) {
          this.startWarn('')
        }
      })
    },
  },
  data: function () {
    return {
      warn: {
        doing: false,
        text: ''
      },
      choosen: 0,
      infoValid: false,
      passwordValid: false,
      rules: {
        name: [
          v => !!v || '',
        ],
        mail: [
          // v => !!v || '',
          v => (v == '' || /.+@.+\..+/.test(v)) || '',
        ],
        password: [
          v => !!v || '',
          v => v.length >= 6 || '6',
        ],
        repeatPassword: [
          v => !!v || '',
          v => v == this.editPassword.new || ''
        ]
      },
      basic: {
        id: "",
        name: "",
        mail: "",
      },
      editInfo: {
        name: "",
        mail: "",
      },
      editPassword: {
        old: "",
        new: "",
        confirm: ""
      },
    };
  },
};
</script>

<style>
</style>